body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.playlist {
  width: 90%;
  max-width: 1900px;
  margin-left: auto;
  margin-right: auto;
  font-family: Roboto, Arial, sans-serif;
  display: flex;
  justify-content: space-between;
}

.playlist__inner {
  width: 70%;
}

.playlist__title {
  color: #000;
  opacity: 0.87;
  font-weight: normal;
}

.playlist__controls {
  display: flex;
  justify-content: space-between;
}

.pagination-controls {
  list-style: none;
  display: flex;
  padding: 0;
  align-items: baseline;
}

.pagination-controls__item {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  margin-left: 2px;
  margin-right: 2px;
  border-radius: 50%;
  cursor: pointer;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.pagination-controls__item:hover,
.pagination-controls__item_state_active {
  border: 1px solid #e0e0e0;
}

.pagination-controls a {
  width: 100%;
  text-align: center;
  padding: 5px 0;
  font: inherit;
}

.pagination-controls a:focus {
  outline: none;
  color: #000;
}

.pagination-controls__btn,
.pagination-controls__break {
  cursor: pointer;
}

.pagination-controls__btn_direction_next {
  margin-left: 5px;
}

.pagination-controls__btn_direction_prev {
  margin-right: 5px;
}

.pagination-controls__btn_state_disabled {
  opacity: 0.3;
  cursor: not-allowed;
}

.sort-controls {
  position: relative;
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
  outline: none;
}

.sort-controls:before,
.sort-controls:after {
  border: 6px solid transparent;
  content: "";
  display: block;
  height: 0;
  width: 0;
}

.sort-controls:before {
  border-bottom-color: rgba(0, 0, 0, 0.57);
  margin-bottom: 3px;
  
}

.sort-controls:after {
  border-top-color: rgba(0, 0, 0, 0.57);
}

.sort-controls.sort-controls_direction_down:before {
  display: none;
}

.sort-controls.sort-controls_direction_down:after {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
  margin-bottom: -12px;
}

.sort-controls.sort-controls_direction_up:before {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
  margin-bottom: 8px;
}

.sort-controls.sort-controls_direction_up:after {
  display: none;
}

.playlist-table {
  border: 1px solid #e0e0e0
}

.playlist-table th {
  background-color: rgba(0, 0, 0, 0.07);
}

.playlist-table td,
.playlist-table th {
  border-right: 1px solid #e0e0e0;
  padding: 15px 20px;
}

.playlist-table td:first-child,
.playlist-table th:first-child {
  width: 30%;
  border-right: 1px solid #e0e0e0;
  padding: 15px 20px;
}

.playlist-table td:nth-child(2),
.playlist-table th:nth-child(2) {
  width: 40%;
}

.playlist-table td:nth-child(3),
.playlist-table th:nth-child(3) {
  width: 20%;
}

.playlist-table td:last-child,
.playlist-table th:last-child {
  width: 10%;
}

.playlist-table__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.playlist-table__header .sort-controls {
  margin-left: 20px;
}

.filters {
  width: 25%;
}

.filters__title {
  color: #000;
  opacity: .87;
  font-weight: normal;
}

.filters__select {
  width: 100%;
  margin-bottom: 30px;
}

.controls-set {
  list-style: none;
  display: flex;
  padding: 0;
  border: 1px solid #e0e0e0;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.controls-set__item {
  box-sizing: border-box;
  cursor: pointer;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-right: 1px solid #e0e0e0;
}

.controls-set__item:hover,
.controls-set__item_state_active {
  background-color: rgba(0, 0, 0, 0.07);
}

.controls-set__item:last-child {
  border-right: none;
}

.controls-set__item button {
  box-sizing: border-box;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
  width: 100%;
  height: 100%;
}

.controls-set__item button:focus {
  outline: none;

}

.spinner,
.spinner:before,
.spinner:after {
  background: #000;
  -webkit-animation: load1 1s infinite ease-in-out;
  animation: load1 1s infinite ease-in-out;
  width: 1em;
  height: 4em;
}
.spinner {
  color: #000;
  text-indent: -9999em;
  position: absolute;
  left: 50%;
  top: 200px;
  font-size: 11px;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
.spinner:before,
.spinner:after {
  position: absolute;
  top: 0;
  content: "";
}
.spinner:before {
  left: -1.5em;
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}
.spinner:after {
  left: 1.5em;
}
@-webkit-keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}
@keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}

